import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { CrudService } from '../services/crud.service';

@Component({
  selector: 'app-usercreate',
  templateUrl: './usercreate.component.html',
  styleUrls: ['./usercreate.component.scss']
})
export class UsercreateComponent implements OnInit {
  createForm: FormGroup;
  users=[];
  index = 0;
  updateIndex = null;
  hide=true;
  constructor(
    private formBuilder: FormBuilder,
    private api: CrudService
  ) { }

  ngOnInit(): void {
    this.createForm = this.formBuilder.group({
      first_name: [null , [Validators.required]],
      last_name: [null , [Validators.required]],
      email: [null , [Validators.required]],
      password: [null , [Validators.required]],
      contact_number:null,
      updated_by:0
  });
    
}
get formControl() {
  return this.createForm.controls;
}

submitForm() {
  if (this.createForm.invalid) {
    console.log('invalid', this.createForm.invalid);
    return;
  }
  console.log('form value',this.createForm.value)

  this.api.createUser(this.createForm.value).subscribe(
    result => {
      console.log('api_result',result);
    },
    error => {
      console.log('error here',error);
    }
    
  );
  
  // console.log('form value', this.registerForm.value);
  // this.users.push( this.registerForm.value);
  // this.registerForm.reset();
  // console.log('users', this.users);
  }


}
